body{
    direction: rtl;
    overflow: hidden;
    height: 100vh;
}
.first-body{
    background-repeat: no-repeat;
    background-image: linear-gradient(to bottom ,#4dcff4 , #ddffd6 );
    @media (max-width:480px) {
        background-image: url('../images/phoneBac.png');
        background-size: cover;
    }
}

.overlay{
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 224;
    visibility: hidden;
    &.active{
        visibility: visible;
    
    }
}
.main-nav{
    position: relative;
    display: flex;
    margin-top: 30px;
    align-items: center;
    position: fixed;
    z-index:270;
    @media (max-width:480px) {
        width: 100%;
    }

    .logo{
        @media (max-width:480px) {
            margin-right: auto;
            margin-left: 30px;
        }

        img{
            width: auto;
            height: auto;
            @media (max-width:991px) {
                width: 80%;   
            }
            @media (max-width:480px) {
                width: 70%;   
            }
        }
    }
    .troger{
        background-color: transparent;
        font-size: 24px;
        color: rgb(39, 50, 78);
        cursor: pointer;
        border: none;
        margin-left: 25px;
        @media (max-width:991px) {
            margin-left: 0;
        }

        outline: none;
    }
    .left-nav{
        position: fixed;
        right: -338px;
        top:0;
        display: flex;
        flex-direction: column;
        text-align: center;
        justify-content: flex-start;
        align-items: center;
        height: 100vh;
        width: 338px;
        @media (max-width:480px) {
            width: 288px;
        }

        background-color: #fff;
        transition: .5s;
        z-index: 100;
        &.active{
            right: 0;

        }
        .header{
            height: 198px;
            width: 100%;
            position: relative; 
            background-color: #e67126;
            margin-bottom: 133px;
            .info{
                position: absolute;
                width: 100%;
                left: 50%;
                top: 70px;
                transform: translateX(-50%);
                font-family: 'Cairo', sans-serif;
                h2{
                    font-size: 20px;
                    color: rgb(84, 88, 110);
                    font-weight: 700;
                    margin: 0;
                    // line-height: 1.2;
                    margin-top: 10px;
                }
                p{
                    margin-top: -1px;
                    font-size: 16px;
                    color: rgba(84, 88, 110, 0.722);
                    font-weight: 400;
                      
                }
            }
        }
        .cross{
            position: absolute;
            left: 15px;
            top: 17px;
            transition: .5s;
            outline: none;
        }
        .list{
            display: flex;
            align-items: center;
            justify-content: center;
            flex-direction: column;
            text-align: center;
            width: 100%;
            padding: 0;
            margin: 0;
            li{
                overflow: hidden;
                width: 100%;
                padding: 0;
                margin-bottom: -2px;
                position: relative;
                height: 53px;
                display: flex;
                justify-content: center;
                align-items: center;
                text-align: center;
                a{
                    font-size: 20px;
                    font-family: 'Cairo', sans-serif;
                    color: #27324a;
                    line-height: 2.65;
                    text-decoration: none;
                    font-weight: 600;
                    position: relative;
                    transition: .5s;
                }
                &.active{
                    background-color: #e67126;
                    a{
                        color: #fff;
                    }
                }
                &:not(:last-child):not(.active){
                    a::before{
                        content: '';
                        position: absolute;
                        width: 283px;
                        height: 1px;
                        background-color: rgba(19, 56, 121,0.239);
                        bottom:0;
                        left: 50%;
                        transform: translateX(-50%);
                        transition: .5s;
                    }
                }
                &::after{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    top: 0;
                    left: 0;
                    transform: scaleX(0);
                    transform-origin: left ;
                    z-index: -1;
                    transition: .5s;
                }
                &:hover:not(.active){
                    a{
                        color: #fff;
                    }
                    &::after{
                        background-color: #e67126;
                        transform: scaleX(1);
                    }
                    a::before{
                        background-color: transparent;
                    }

                }
            }
        }
    }    
}
