            justify-content: center;
            margin-top: auto;
            font-family: 'Jazeera';
            font-weight: 500;
            .logInput{
                width: 216px;
                height: 60px;
    
                border: none;
                border-radius: 100px 90px /  28px 65px 91px 66px;
                outline: none;
                padding-left: 5px;
                padding-right: 5px;
                text-align: center;
                margin-bottom: 33px;
