                font-size: 30px;
                color: rgba(#000,.2);
            }
            .logbtn{
                border-radius: 100px 90px /  28px 65px 91px 66px;
                outline: none;
                width: 216px;
                height: 60px;
                border: none;
                background-color: #89bd36;
                color: #fff;
                position: relative;
                font-size: 30px;
                cursor: pointer;
                overflow: hidden;
                transition: .5s;
                &::after{
                    content: '';
                    position: absolute;
                    width: 100%;
                    height: 100%;
                    background-color: transparent;
                    top: 0;
                    left: 0;
                    right: 0;
                    bottom: 0;
                    transform: scaleX(0);
                    transform-origin: left ;
                    transition: .5s;
                    z-index: 1;
                }
                &:hover{
                    &::after{
                        background-color: #e67126;
                        transform: scaleX(1);
                    }
                }
                & span{
                    z-index: 2;
                    position: relative;
                }
                & span::before{
                    position: absolute;
                    content: '';
                    bottom: 0;
                    left: -20px;
                    width: 50px;
                    height: 30px;
                    border-radius: 100%;
                    background-color: rgba(#fff,.1);
                }

                & span::after{
                    position: absolute;
                    content: '';
                    bottom: 10px;
                    left: -13px;
                    width: 15px;
                    height: 1px;
                    background-color:#fff;
                    filter: blur(.6px)
                }

            }
        }
